.chatbot-container {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1000; /* Ensure it appears above other elements */
}

.floating-icon {
    font-size: 40px; /* Size of the floating icon */
    cursor: pointer; /* Pointer cursor on hover */
    background-color: #007bff; /* Background color */
    color: white; /* Icon color */
    border-radius: 50%; /* Circular shape */
    padding: 15px; /* Padding around the icon */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Shadow effect */
}

.chatbot-card {
    position: fixed; /* Make it float */
    bottom: 20px; /* Position from the bottom */
    right: 20px; /* Position from the right */
    width: 350px; /* Width of the card */
    background-color: #ffffff; /* Background color */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Shadow effect */
    padding: 20px; /* Padding inside the card */
    z-index: 1000; /* Ensure it appears above other elements */
    display: flex;
    flex-direction: column; /* Stack elements vertically */
    transition: all 0.3s ease; /* Smooth transition */
}

.close-button {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    float: right; /* Position the close button to the right */
}

.chat-window {
    flex-grow: 1; /* Allow the chat window to grow */
    overflow-y: auto; /* Enable scrolling if content overflows */
    max-height: 250px; /* Set a maximum height */
    margin-bottom: 10px; /* Space between chat window and input */
    padding: 10px; /* Padding inside the chat window */
    background-color: #f9f9f9; /* Light background for the chat window */
    border-radius: 5px; /* Rounded corners for the chat window */
}

.message {
    margin-bottom: 10px; /* Space between messages */
    display: flex; /* Use flexbox for alignment */
}

.message.user {
    justify-content: flex-end; /* Align user messages to the right */
}

.message.bot {
    justify-content: flex-start; /* Align bot messages to the left */
}

.message div {
    max-width: 70%; /* Limit message width */
    padding: 10px; /* Padding for message bubbles */
    border-radius: 10px; /* Rounded corners for message bubbles */
}

.message.user div {
    background-color: #007bff; /* User message background color */
    color: white; /* User message text color */
}

.message.bot div {
    background-color: #e0e0e0; /* Bot message background color */
    color: black; /* Bot message text color */
}

input {
    padding: 10px; /* Padding for the input field */
    border: 1px solid #ccc; /* Border for the input field */
    border-radius: 5px; /* Rounded corners for the input field */
    margin-bottom: 10px; /* Space between input and button */
    font-size: 14px; /* Font size */
}

button {
    padding: 10px; /* Padding for the button */
    background-color: #007bff; /* Button color */
    color: white; /* Text color */
    border: none; /* Remove border */
    border-radius: 5px; /* Rounded corners for the button */
    cursor: pointer; /* Pointer cursor on hover */
    transition: background-color 0.3s ease; /* Smooth transition */
}

button:hover {
    background-color: #0056b3; /* Darker color on hover */
}

button:focus {
    outline: none; /* Remove outline on focus */
}